import React from 'react';
import {VerticalNavigationSubList} from '@here/hds-vertical-navigation';
import {HDSListProperties} from '../List/HDSList.js';

// eslint-disable-next-line no-new
new VerticalNavigationSubList();

interface HDSVerticalNavigationSubListProperties extends HDSListProperties {}

const HDSVerticalNavigationSubList = React.forwardRef(
  (props: HDSVerticalNavigationSubListProperties, ref) => {
    const {children, className, ...rest} = props;

    return (
      <hds-vertical-navigation-sub-list ref={ref} {...(className && {class: className})} {...rest}>
        {children}
      </hds-vertical-navigation-sub-list>
    );
  }
);

export default HDSVerticalNavigationSubList;
